/* src/App.css */

body {
  background-color: #f5f7fa;
  font-family: "Inter", sans-serif;
  color: #333;
  margin: 0;
  padding: 0;
}

.container {
  max-width: 800px;
  margin: 60px auto;
  background: white;
  padding: 40px;
  border-radius: 20px;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.08);
}

h1 {
  text-align: center;
  font-size: 2.2rem;
  margin-bottom: 1.5rem;
  color: #1a237e;
}

h2 {
  font-size: 1.5rem;
  color: #374151;
  margin-bottom: 1rem;
}

p {
  font-size: 1.1rem;
  line-height: 1.6;
  white-space: pre-line;
}

button {
  background-color: #1a73e8;
  color: white;
  border: none;
  padding: 10px 24px;
  font-size: 1rem;
  border-radius: 10px;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

button:hover {
  background-color: #1557b0;
}

.footer {
  text-align: center;
  margin-top: 50px;
  color: #777;
  font-size: 0.9rem;
}
